import React, { Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";

const ModelServers = React.lazy(() => import('./modelServers/servers'));
const ModelRegistries = React.lazy(() =>
  import('./modelRegistries/registries')
);
const RegistryVersions = React.lazy(() =>
  import('./modelRegistries/registry-versions')
);

const FreePage = React.lazy(() => import("./free-page.js"));

const Models = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Route
        path={`${match.url}/`}
        render={(props) => <FreePage {...props} />}
      ></Route>
      {/* <Route
        path={`${match.url}/servers`}
        render={(props) => <ModelServers {...props} />}
      />
      <Route
        exact
        path={`${match.url}/registries`}
        render={(props) => <ModelRegistries {...props} />}
      />
      <Route
        path={`${match.url}/registries/:modelIndex/:modelName`}
        render={(props) => <RegistryVersions {...props} />}
      /> */}

      <Redirect to="/error" />
    </Switch>
  </Suspense>
);

export default Models;
